<template>
  <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      label="系统code"
      width="180">
      <template scope="scope">
        <span style="margin-left: 10px">{{ scope.row.systemCode }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="系统名称"
      width="180">
      <template scope="scope">
        <span style="margin-left: 10px">{{ scope.row.systemName }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="负责人"
      width="180">
      <template scope="scope">
        <span style="margin-left: 10px">{{ scope.row.principalUser }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="环境列表"
      width="180">
      <template scope="scope">
        <ul>
          <li v-for="env in scope.row.envList">
            {{ env.envName }}:{{ env.envAddress }}
          </li>
        </ul>
      </template>
    </el-table-column>
    <el-table-column
      label="更新时间"
      width="180">
      <template scope="scope">
        <el-popover  placement="top">
          <div slot="reference" class="name-wrapper">
            <el-tag>{{ scope.row.gmtCreate }}</el-tag>
          </div>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template scope="scope">
        <el-button
          size="small"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="small"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import axios from 'axios';


  export default {
    data: () => ({
    tableData: [],
      errors: []
    }),
    created() {
      axios.get(`/system/page`)
        .then(response => {
        // JSON responses are automatically parsed.

        this.tableData =[{
          "envList": [
            {
              "id": 0,
              "systemCode": "",
              "envName": "正式环境",
              "envAddress": "10.2.2.1",
              "gmtCreate": "",
              "gmtModify": ""
            }
          ],
          "gmtCreate": "2016-2-12",
          "gmtModify": "2019-27-3",
          "id": 0,
          "principalUser": "fuzeren",
          "systemCode": "jkflasjkldf",
          "systemName": "jaajasss"
        }];
    })
    .catch(e => {
        this.errors.push(e)
    })},
    methods: {
      handleEdit(index, row) {
        this.tableData[0] = {
          date: '2016-5-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        };
      },
      handleDelete(index, row) {
        console.log(index, row);
      }
    }}

</script>



